<template lang="pug">
    .programmes__content
        .programmes__title Учебные программы
        ul.programmes__filter
            li.programmes__filter-item
                label.label__filter-teacher(for='subjects') Выберите предмет:
                select(id='subjects' name='subjects' v-model="selectedSubject").programmes__filter-select
                    option(:value='0') Все
                    option(:value='item.id' v-for='item in departmentSubjects') {{item.name}}
            li.programmes__filter-item
                label.label__filter-teacher(for='classes') Выберите класс:
                select(name='classes' v-model="selectedClass").programmes__filter-select
                    option(:value='0') Все
                    option(:value='item' v-for='item in 12') {{item}}
            li.programmes__filter-item
                label.label__filter-teacher(for='languages') Выберите язык:
                select(name='languages' v-model="selectedLanguage").programmes__filter-select
                    option(value='') Все
                    option(value='Казахский') Казахский
                    option(value='Русский') Русский
        ul.programmes__list
            li.programmes__item(v-for="item,idx in filteredProgrammes" :key="item.id")
                programme-item(
                    :index='idx'
                    :programme='item'
                )
        button(@click.prevent='addProgramme' id='addProgramme').add__programme Добавить программу
        add-programme-form(
            :user='user'
            v-if='addProgrammeFormVisible'
            :programmes='programmes'
            :subjects='departmentSubjects'
            v-on:eventNewProgramme='eventNewProgramme'
            )
</template>
<script>
import { mapState, mapActions } from "vuex";

export default {
    components: {
        addProgrammeForm: () => import("../../teacher/programmes/add-programme-form"),
        programmeItem: () => import("../../teacher/programmes/programme-item")
    },
    created() {
        this.fetchTeacherProgrammes(this.user.id),
        this.fetchDepartmentSubjects(this.user.teacherDepartment.id)
    },
  data () {
    return {
        selectedLanguage: '',
        selectedSubject: 0,
        selectedClass: 0,
        addProgrammeFormVisible: false,
        programmePointsVisible: false
    }
  },  
   computed: {
        ...mapState ("user", {
            user: state => state.user}),
        ...mapState ("programmes", {
            programmes: state => state.programmes}),
        ...mapState ("departmentSubjects", {
            departmentSubjects: state => state.departmentSubjects}),
        filteredProgrammes: function() {
            return this.programmes
            //filter by subject
            .filter(programme => {
                return this.selectedSubject == 0 || programme.group.subject.id== this.selectedSubject;
            })
            //filter by class
            .filter(programme => {
                return this.selectedClass == 0 || programme.group.groupClass == this.selectedClass;
            })
            //filter by language
            .filter(programme => {
                return this.selectedLanguage == '' || programme.group.language == this.selectedLanguage;
            })
        }
    },       
  methods: {
        ...mapActions("programmes", ["fetchTeacherProgrammes"]),
        ...mapActions("departmentSubjects", ["fetchDepartmentSubjects"]),
      addProgramme() {
          this.addProgrammeFormVisible=!this.addProgrammeFormVisible
          document.getElementById('addProgramme').setAttribute("disabled", "true")
      },
      eventNewProgramme() {
          this.addProgrammeFormVisible=!this.addProgrammeFormVisible
          document.getElementById('addProgramme').removeAttribute("disabled")
      }
  }
}
</script>
