    margin-bottom: 15px;
    &:hover {
        opacity: .8;
        transition: .5s;
        cursor: pointer;
    }
}
.programmes__filter-select {
    background-color: #ffe9ce;
    border-radius: 5px;
    padding: 4px;
    width: 125px;
}
.programmes__filter {
    justify-content: space-between;
